import React, { useState, useEffect } from "react";

import buildCalendar from "./build";
import dayStyles, { isSunday } from "./styles";
import MonthHeader from "./months";


export default function Calendar({ value, onChange }) {
    const [calendar, setCalendar] = useState([]);

    useEffect(() => {
        setCalendar(buildCalendar(value));
    }, [value]);

    return (
        <div className="dates">
            <MonthHeader value={value} setValue={onChange} />
            <div className="dayname">
                <div>Mon</div>
                <div>Tue</div>
                <div>Wed</div>
                <div>Thu</div>
                <div>Fri</div>
                <div>Sat</div>
                <div>Sun</div>
            </div>
            <div style={{ position: "relative", height: "17em" }}>
                {calendar.map((week) => (
                    <div key={week} className="days">
                        {week.map((day) => (
                            <div key={day} className="day" onClick={() => !isSunday(day) && onChange(day)}>
                                <div className={dayStyles(day, value)}>{day.format("D").toString()}</div>
                            </div>
                        ))}
                    </div>
                ))}
            </div>
        </div>
    );
};

