import React from "react";

export default function CalendarMonths({ value, setValue }) {
    function currMonth() {
        return value.format("MMMM");
    }

    function currYear() {
        return value.format("YYYY");
    }

    function prevMonth() {
        return value.clone().subtract(1, "month");
    }

    function nextMonth() {
        return value.clone().add(1, "month");
    }

    function thisMonth() {
        return value.isSame(new Date(), "month");
    }

    function threeMonths() {
        const plusThree = 4;//new Date().getMonth() + 1;

        return value.format("M").toString() === plusThree.toString()
    }

    return (
        <div className="month">
            <div className="arrows prev-mth" onClick={() => !thisMonth() && setValue(prevMonth())}>&lt;</div>
            <div style={{ color: "232323", fontWeight: "600" }} className="mth">
                {currMonth()} {currYear()}
            </div>
            <div className="arrows next-mth" onClick={() => !threeMonths() && setValue(nextMonth())}>&gt;</div>
        </div>
    )
}