import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { listTimes } from "./../../../Redux/Actions/OrderActions";
import Loading from "./../../LoadingError/Loading";

import buildHours from "./hours";
import hourStyles, { isBusy, beforeHours, selectedOrder } from "./stylesHours";

export default function CalendarHours({ value, onChange, onOrder, selectedService, props }) {
    const [hours, setHours] = useState([]);

    const { loading, error, orders } = props;
    
    useEffect(() => {
        setHours(buildHours(value));
        
    }, [ value, selectedService]);

    return (
        <>  {
            loading ? (<Loading />) : error ? (<Message variant="alert-danger">{error}</Message>) : (
                <div className="hour-picker">
                    {hours.map((hourss) => (
                        <div key={hourss} className="hours">
                            {hourss.map((hour) => (
                                <div key={hour} className="hour" onClick={() => { onOrder(selectedOrder(value, hour, props[2])); onChange(hour); }}>
                                    <div className={hourStyles(hour, value, props[2].map((h) => h.serviceTime), props[2].map((s) => s.service), selectedService)}>{hour.format('HH:mm').toString()}</div>
                                </div>
                            ))}
                        </div>
                    ))}
                </div>
            )}
        </>
    )
}