import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import buildCalendar from "./build";
import dayStyles, { beforeToday, busyDay, isSunday, threeMonths } from "./styles";
import MonthHeader from "./months";
import { listRemovedDate } from "../../../Redux/Actions/RemovedDateActions";
import Loading from "../../LoadingError/Loading";


export default function Calendar({ value, onChange }) {
    const [calendar, setCalendar] = useState([]);

    const dispatch = useDispatch();

    const removedDateList = useSelector((state) => state.removedDateList);
    const { loading, error, date } = removedDateList;

    useEffect(() => {
        setCalendar(buildCalendar(value));
        dispatch(listRemovedDate());
    }, [dispatch, value]);

    return (
        <> {loading ? (<Loading />) : error ? (<Message variant="alert-danger">{error}</Message>) : (
            <div className="dates">
                <MonthHeader value={value} setValue={onChange} />
                <div className="dayname">
                    <div>Mon</div>
                    <div>Tue</div>
                    <div>Wed</div>
                    <div>Thu</div>
                    <div>Fri</div>
                    <div>Sat</div>
                    <div>Sun</div>
                </div>
                <div style={{ position: "relative", height: "17em" }}>
                    {calendar.map((week) => (
                        <div key={week} className="days">
                            {week.map((day) => (
                                <div key={day} className="day" onClick={() => !busyDay(day, date.map((d) => d.removedDate)) && !threeMonths(value) && !isSunday(day) && !beforeToday(day) && onChange(day)}>
                                    <div className={dayStyles(day, value, date.map((d) => d.removedDate))}>{day.format("D").toString()}</div>
                                </div>
                            ))}
                        </div>
                    ))}
                </div>
            </div>
        )}
        </>
    );
};

//export default calendar;