import axios from "axios";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { listTimes } from "./../../../Redux/Actions/OrderActions";
import Loading from "./../../LoadingError/Loading";

import buildHours from "./hours";
import hourStyles, { isBusy, beforeHours } from "./stylesHours";

export default function CalendarHours({ value, onChange, selectedService }) {
    const [hours, setHours] = useState([]);
    /*const [serviceData, setServiceData] = useState([]);
       const serviceDetails = useSelector((state) =>state.serviceDetails);
       const { service, success, error } = serviceDetails;
       useEffect(() => {
           const fetchServices = async () => {
               const { data } = await axios.get("/api/services");
               setServiceData(data);
           };
           fetchServices();
       }, []);*/


    const dispatch = useDispatch();
    const orderTimes = useSelector((state) => state.orderTimes);
    const { loading, error, order } = orderTimes;

    useEffect(() => {
        setHours(buildHours(value));
        dispatch(listTimes());
    }, [dispatch, value, selectedService]);

    return (
        <> {
            loading ? (<Loading />) : error ? (<Message variant="alert-danger">{error}</Message>) : (
                <div className="hour-picker">
                    {hours.map((hourss) => (
                        <div key={hourss} className="hours">
                            {hourss.map((hour) => (
                                <div key={hour} className="hour" onClick={() => !isBusy(hour, order.map((h) => h.serviceTime), order.map((s) => s.service), selectedService) && !beforeHours(hour) && onChange(hour)}>
                                    <div className={hourStyles(hour, value, order.map((h) => h.serviceTime), order.map((s) => s.service), selectedService)}>{hour.format('HH:mm').toString()}</div>
                                </div>
                            ))}
                        </div>
                    ))}
                </div>
            )}
        </>
    )
}